body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  background-color: #b2ebf2;
  padding: 40px;
  border-radius: 10px;
  text-align: center;
  width: 90%;
  max-width: 800px;
  margin: auto;
}

h1 {
  font-size: 4rem;
  color: #2c6d6a;
  margin-bottom: 30px;
  font-weight: bold;
}

label {
  font-size: 2rem;
  color: #2c6d6a;
  margin-right: 15px;
  font-weight: bold;
}

input {
  font-size: 1.5rem;
  padding: 10px 20px;
  border-radius: 30px;
  border: none;
  background-color: #2c6d6a;
  color: white;
  font-weight: bold;
  text-align: center;
  width: 250px;
}

body {
  background-color: #e0f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

input {
  display: block;
  margin: 10px auto;
}

button {
  font-size: 1.5rem;
  margin-top: 30px;
  padding: 12px 40px;
  background-color: #1e5554;
  color: white;
  border: none;
  border-radius: 30px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #144241;
}

.form-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.form-row label {
  width: 220px;
  text-align: right;
}

.form-row input {
  margin: 0; /* es para eliminar margin auto */
}
/* agrego los colores para los mensajes de error del registro*/
.mensaje-error {
  color: red;
  font-weight: bold;
  margin-top: 10px;
}

.mensaje-exito {
  color: green;
  font-weight: bold;
  margin-top: 10px;
}

/* Fondo general */
body {
  margin: 0;
  padding: 0;
  background-color: #c8e9ea;
  font-family: 'Georgia', serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Contenedor principal */
.menu-principal-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.curso-label {
  font-size: 2rem;
  font-weight: bold;
  color: #255b5b; 
}

/* Select */
.curso-select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  background-color: #255b5b; 
  color: #ffffff; 
  font-size: 1.5rem;
  font-family: 'Georgia', serif;

  padding: 15px 50px 15px 20px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  outline: none;

  position: relative;
}

/* Opciones */
.curso-select option {
  background-color: #255b5b;
  color: #ffffff;
  font-size: 1.2rem;
}

/* Flecha  */
.curso-select {
  background-image: url("data:image/svg+xml;utf8,<svg fill='%23c8e9ea' height='20' viewBox='0 0 24 24' width='20' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>");
  background-repeat: no-repeat;
  background-position: right 15px center;
  background-size: 20px;
}

.curso-select::-webkit-scrollbar {
  width: 6px;
}

.curso-select::-webkit-scrollbar-track {
  background: #255b5b;
}

.curso-select::-webkit-scrollbar-thumb {
  background: #c8e9ea;
  border-radius: 10px;
}

.menu-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

.welcome-container {
  text-align: center;
}

.welcome-title {
  font-size: 3rem;
  color: #255b5b;
  margin: 0;
}

.welcome-subtitle {
  font-size: 1.5rem;
  color: #255b5b;
  margin: 10px 0 0 0;
  font-weight: normal;
}

.menu-wrapper {
  background: #ffffff;
  padding: 40px 60px;
  border-radius: 20px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.15);
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-admin-card { max-width:600px; border-radius:30px; }
.large-admin{ font-size:3.5rem; }
.btn-group{ display:flex; flex-direction:column; gap:20px; width:100%; }
.btn-group button{ width:100%; border-radius:50px; }
.lista-title{ font-size:1.8rem; margin-top:40px; }
.user-list li{ font-size:1.5rem; margin-top:10px; }

/* --- AJUSTE PARA FORMULARIO DE REGISTRO --- */

/* Contenedor del formulario dentro del cuadro blanco */
.menu-wrapper form {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Alinea todo a la izquierda */
}

/* Filas del formulario */
.form-row {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* ✅ Evita centrado */
  margin-bottom: 20px;
  width: 100%;
}

/* Etiquetas alineadas a la izquierda */
.form-row label {
  width: 220px; /* Mantiene proporción bonita */
  text-align: left; /* ✅ Alinea texto a la izquierda */
  font-size: 2rem;
  color: #2c6d6a;
  font-weight: bold;
  margin-right: 15px;
}

/* Inputs alineados correctamente */
.form-row input {
  margin: 0;
  text-align: left; /* ✅ Evita centrado del texto */
  width: 250px;
  font-size: 1.5rem;
  padding: 10px 20px;
  border-radius: 30px;
  border: none;
  background-color: #2c6d6a;
  color: white;
  font-weight: bold;
}

/* Botón centrado como en el diseño */
button {
  display: block;
  margin: 30px auto 0 auto;
}

/* Ajuste del botón de redirección */
.boton-redirigir {
  display: block;
  text-align: center;
  width: 100%;
  margin-top: 20px;
}

.subtitulo-materias {
  font-size: 2rem;
  color: #2c6d6a;
  font-weight: bold;
  margin: 20px 0 10px 0;
  text-align: left;
}

.materia-curso-container {
  display: flex;
  gap: 20px;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.materia-input,
.curso-input {
  background-color: #2c6d6a;
  color: white;
  font-size: 1.5rem;
  padding: 10px 20px;
  border: none;
  border-radius: 30px;
  font-weight: bold;
  text-align: center;
  width: 200px;
}

.botones-materias {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}


